import React, { Component } from 'react'
import styled from 'styled-components'

class Jumbo extends Component {
  constructor(props) {
    super(props)
    this.state = {
      header: false
    }
  }

  componentDidMount() {
    window.addEventListener('scroll', (e) => {
      if (window.scrollY > 0) {
        this.setState({
          header: true
        })
      } else {
        this.setState({
          header: false
        })
      }
    })
  }

  render() {
    return (
      <Main id="home">
        <JumboImg src={require('../assets/jumbo.png')} />

        <Header header={this.state.header}>
          <Logo src={require('../assets/logo.png')} />

          <Menu>
            <Item header={this.state.header} href="#home">
              Home
            </Item>
            <Item header={this.state.header} href="#opportunity">
              Opportunity
            </Item>
            <Item header={this.state.header} href="#solution">
              Solution
            </Item>
            <Item header={this.state.header} href="#technology">
              Technology
            </Item>
            <Item header={this.state.header} href="#team">
              Team
            </Item>
            <Item header={this.state.header} href="#bottom">
              Blog
            </Item>
            <WhiteItem header={this.state.header} href="#bottom">
              Contact
            </WhiteItem>
          </Menu>
        </Header>

        <SplitSection>
          <Left>
            <Text1>Cannabis Profitability Powered By Blockchain</Text1>
            <Text2>
              Cannatrac is dedicated to setting the cannabis industry up for
              longer-term profitability through better compliance and brand
              management enabled by a complete and verifiable chain of custody
              on the blockchain.
            </Text2>
            <CTABtn href="https://calendly.com/cannatrac/30min" target="_blank">
              Schedule a demo today
            </CTABtn>
          </Left>
          <Right>
            <DemoImg src={require('../assets/demoImg.png')} />
          </Right>
        </SplitSection>
      </Main>
    )
  }
}

export default Jumbo

const Main = styled.div`
  padding-top: 100px;
`

const JumboImg = styled.img`
  width: 60%;
  position: absolute;
  z-index: -10;
  top: 0;
  right: 0;
`

const Header = styled.div`
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  width: 90%;
  padding: 0 5%;
  background-color: ${({ header }) => (header ? 'white' : 'transparent')};
`

const Logo = styled.img`
  width: 66px;
  height: 66px;
  margin-top: 10px;
  margin-bottom: 10px;
`

const Menu = styled.div`
  display: flex;
  align-items: center;
`

const Item = styled.a`
  font-weight: 300;
  font-size: 16px;
  margin: 0 20px;
  text-decoration: none;
  color: ${({ header }) => (header ? '#4c3199' : 'white')};
`

const WhiteItem = styled.a`
  text-decoration: none;
  color: ${({ header }) => (header ? 'white' : '#4c3199')};
  font-weight: 300;
  font-size: 16px;
  margin: 0 20px;
  background-color: ${({ header }) => (header ? '#4c3199' : 'white')};
  padding: 4px 30px;
  border-radius: 40px;
`

const SplitSection = styled.div`
  display: flex;
  flex-direction: row;
  margin-top: 100px;

  @media (max-width: 500px) {
    flex-direction: column;
  }
`

const Left = styled.div`
  flex: 1;
  padding-top: 30px;
`

const Text1 = styled.p`
  margin: 0;
  color: #41129a;
  font-size: 44px;
  font-weight: bold;
  margin-bottom: 20px;

  @media (max-width: 500px) {
    font-size: 32px;
  }
`
const Text2 = styled.p`
  margin: 0;
  color: #270a5c;
  font-size: 20px;
  margin-bottom: 30px;

  @media (max-width: 500px) {
    font-size: 16px;
  }
`

const CTABtn = styled.a`
  text-decoration: none;
  margin: 0;
  margin-top: 50px;
  padding: 7px 35px;
  background-color: #41129a;
  border-radius: 40px;
  color: white;
  border: 0;
  font-family: 'Futura', sans-serif;
  font-size: 16px;
`

const Right = styled.div`
  flex: 1;
`

const DemoImg = styled.img`
  width: 90%;

  @media (max-width: 500px) {
    margin-top: 50px;
    font-size: 32px;
  }
`
